h1, h2, h3, h4, h5, h6, p, td, li, a, textarea {
	font-family: "Rubik";
	font-weight: 500;
}

h1 {
	font-size: 40px;
}

h2 {
	font-size: 30px;
}

h3 {
	font-size: 25px;
}

p {
	font-size: 16px;
}

.teaser__text {
	font-size: 16px;
	font-weight: 400;
	line-height: 28px;
}

.contact__name {
	font-size: 11px;
	font-weight: 500;
	line-height: 13px;
}

.contact__status {
	font-size: 8px;
	font-weight: 400;
	line-height: 9px;
}

.text__text, .offer__text {
	font-size: 10px;
	font-weight: 300;
	line-height: 15px;
}

.offer__text {
	line-height: 1.5;
}

.message__price {
	font-weight: 700;
	line-height: 11px;
}

.input__textarea {
	font-size: 11px;
	font-weight: 400;

	&::placeholder {
		font-size: 11px;
		font-weight: 400;
		line-height: 1.5;
		color: $grey;
	}

}
.icon {
	font-family: 'Nucleo';
	display: inline-block;
	font: normal normal normal 1em/1 'Nucleo', sans-serif;
	color: inherit;
	flex-shrink: 0;
	speak: none;
	text-transform: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	&--sm { font-size: 12px; }
	&--md { font-size: 16px; }
	&--lg { font-size: 20px; }

	&--white { color: $white }
	&--black { color: $black }

}

.icon-arrow-left::before { content: "\ea02"; }
.icon-arrow-right::before { content: "\ea03"; }
.icon-menu::before { content: "\ea04"; }
.icon-checkmark::before { content: "\ea05"; }

* {
	box-sizing: border-box;
	scrollbar-width: none;
	scroll-behavior: smooth;
}

*::-webkit-scrollbar {
	display: none;
	-webkit-appearance: none;
}

body {
	background-color: $white;
}
.chat-app {
	width: 100%;
	max-width: 100vw;
	min-height: 100vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	gap: 64px;
	position: relative;
	padding: 32px;
	overflow: hidden !important;

	&__phone, .phone {
		position: relative;
		width: 100%;
		max-width: 247px;
		height: 505px;
		border: 10px solid $white;
		border-radius: 30px;
		flex-shrink: 0;
		background-color: $lightgrey-75;
		position: relative;
		box-shadow: 0px 30px 60px -10px $shadowcolor;
		overflow: hidden;

		&__header, .header {
			grid-area: header;
			background: $gradient;
			width: 100%;
			height: 80px;
			display: flex;
			flex-direction: row;
			justify-content: flex-start;
			align-items: center;
			gap: 16px;
			padding: 32px 16px 16px 16px;
			box-shadow: 0px 0px 13px $shadowcolor;

			&__back {
				margin-top: 4px;
				cursor: pointer;
			}
			
			&__contact, .contact {
				display: grid;
				grid-template: 
				"avatar name"
				"avatar status";
				
				&__avatar {
					grid-area: avatar;
					width: 32px;
					height: 32px;
					border: 2px solid $white;
					border-radius: 50%;
					display: block;
					margin-right: 8px;
				}
				
				&__name {
					grid-area: name;
					line-height: 1;
					align-self: center;
					color: $white;
				}
				
				&__status {
					grid-area: status;
					line-height: 1;
					align-self: center;
					color: $lightmagenta;
				}

			}
			
			&__menu {
				margin-left: auto;
				margin-top: 4px;
				cursor: pointer;
			}

		}

		&__inner {
			height: 100%;
			height: calc(100% - (50px));
			overflow-y: scroll;
		}
		
		&__messages {
			padding: 8px;
			height: calc(100% - (80px));
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			overflow: scroll;
			flex-grow: 0;
			border-bottom: 1px solid $lightgrey-100;
		}
		
		&__message, .message {
			max-width: 75%;
			margin-bottom: 10px;
			
			&:last-child {
				margin-bottom: 0;
			}
			
			&--left {
				align-self: flex-start;
				border-radius: 10px 10px 4px 10px;
			}
			
			&--right {
				border-radius: 10px 10px 4px 10px;
				align-self: flex-end;
			}
			
			&--white {
				background-color: $white;
				color: $black;
				box-shadow: 0px 10px 5px -5px rgba(62, 39, 83, 0.045318);
			}
			
			&--magenta {
				background-color: $magenta;
				color: $violet;
			}
			
			&--violet {
				background-color: $violet;
				color: white;
			}
			
			&--text, .text {
				padding: 10px;
				background-color: white;
				border-radius: 10px;	
			}

			&--offer, .offer {
				display: flex;
				flex-direction: row;
				justify-content: flex-start;
				padding: 10px;
				width: 100%;
				align-items: center;
				cursor: pointer;

				&__checkbox, .checkbox {
					width: 14px;
					height: 14px;
					min-width: 14px;
					min-height: 14px;
					border-radius: 50%;
					margin-right: 8px;
					border: 1px solid $magenta;
					position: relative;

					&__icon {
						font-size: 0px;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						z-index: 5;
					}
						
					&::after {
						content: "";
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						border-radius: 50%;
						width: 0%;
						height: 0%;
						background-color: $green;
					}

					&--checked {

						.checkbox__icon {
							font-size: 6px;
						}

						&::after {
							width: 100% !important;
							height: 100% !important;	
						}

					}

				}

				&__text {
					margin-top: 2px;
					margin-right: 8px;
				}

				&__price {
					margin-left: auto;
				}

			}

			&--media, .media {
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				justify-content: flex-end;
				gap: 10px;

				img {
					display: block;
					width: 40px; 
					height: 40px;
					border-radius: 8px;
					object-fit: cover;

					&:last-child {
						margin-right: 0;
					}

				}

			}

		}
		
		&__input, .input {
			height: 32px;
			width: calc(100% - (2 * 8px));
			display: block;
			margin-top: 16px;
			margin: 8px;
			padding: 8px;
			position: relative;
			
			&__textarea {
				border-radius: 18px;
				position: absolute; 
				inset: 0 0 0 0;
				background-color: $white;
				padding: 10px 16px;
			}
	
		}

		&::before {
			content: "";
			width: 60%;
			height: 20px;
			border-radius: 0px 0px 14px 14px;
			background-color: $white;
			position: absolute; 
			top: 0;
			left: 50%;
			transform: translateX(-50%)
		}
	}
	
	&__teaser, .teaser {
		max-width: 445px;

		&__title {
			margin-bottom: 24px;
		}

		&__text {
			color: $grey;
		}

	}

	&::before, 
	&::after {
		content: "";
		position: absolute;
		max-width: calc(50vw - 200px);
		width: 41vw;
		height: 145%;
		background: $gradient;
		border-radius: 50vw;
		z-index: -1;
	}

	&::before {
		bottom: 15vh;
		left: -5vw;
	}
	
	&::after {
		top: 15vh;
		right: -5vw;
		opacity: 0.03
	}

	@media (max-width: 700px) {
		flex-direction: column;

		&__phone, .phone {
			max-width: 350px;
			height: 545px;
		}

		&__teaser, .teaser {
			margin-bottom: 32px;
	
			&__title {
				text-align: center;
			}
	
			&__text {
				text-align: center;
			}
	
		}

		&::before, 
		&::after {
			content: "";
			position: absolute;
			max-width: calc(100vw);
			width: 100vw;
		}
	
		&::before {
			bottom: 40vh;
			left: -45%;
		}
		
		&::after {
			top: 40vh;
			right: -45%;
			opacity: 0.03
		}

	}
	@media (max-width: 350px) {
		
		&__message, .message {
			max-width: 90% !important;
		}

	}

}